<template>
  <div class="page page-withdraw-process">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="提现进度"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-progress">
      <div class="list-wrap">
        <tab-layout :links="tabLinks" :bar-width="barWidth" bg-color="#FFF"
        use-content-slot="true" :on-tab-click="onTabClick"></tab-layout>
        <div class="time-note">最慢48小时内完成审核 <router-link class="ceb" to="/withdraw/arrived-example" >到账示例</router-link></div>

        <ProcessList v-if="activeIndex == 0" :list="kabiList" :infinite="infiniteKabi"/>
        <ProcessList v-if="activeIndex == 1" :list="jfbList" :infinite="infiniteJfb"/>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  @import "../../../scss/mixins";

  .page-withdraw-process {
    background: #fff;

    .withdraw-progress {
      background: #FFFFFF !important;
      margin-top: 50px;
      text-align: center;

      .list-wrap {
        position: relative;
        height: 100%;

        .time-note {
          background: #FFF8EE;
          font-size: 12px;
          color: #3A3A3A;
          line-height: 40px;
          margin: 20px 40px 30px;
          height: 40px;

          .ceb {
            color: #ef502b;
          }
        }
      }

      @include font-regular();
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapActions} from 'vuex'
  import ProcessList from './ProcessList.vue'
  import TabLayout from './TabLayout.vue'

  const LIMIT = 10

  export default {
    components: {
      AnimateHeader,
      TabLayout,
      ProcessList
    },

    data () {
      return {
        kabiList: [],
        jfbList: [],
        noMoreKabi: false,
        noMoreJfb: false,
        kabiOffset: 0,
        jfbOffset: 0,
        barWidth: 375,
        activeIndex: 0,
        tabLinks: [
          {
            to: { path: '/withdraw/process/kabi' },
            text: '咖币'
          },
          {
            to: { path: '/withdraw/process/jifenbao' },
            text: '集分宝'
          }
        ]
      }
    },

    mounted () {
      this.fetch()
      this.activeIndex = _.indexOf(['kabi', 'jifenbao'], this.$route.params.type) || 0
    },

    methods: {
      ...mapActions('withdraw', [
        'WithdrawApplyListAll',
        'withdrawApplyList'
      ]),

      fetch () {
        this.WithdrawApplyListAll()
          .then(res => res.data && res.data.payload)
          .then(payload => {
            let kabi_list = payload.kabi_list
            let jfb_list = payload.jfb_list
            // 咖币提现列表
            this.kabiList = this.kabiList.concat(kabi_list)
            this.kabiOffset = kabi_list.length > 0 && kabi_list[kabi_list.length - 1].id
            this.noMoreKabi = this._numberList(kabi_list) < LIMIT

            // 集分宝提现列表
            this.jfbList = this.jfbList.concat(jfb_list)
            this.jfbOffset = jfb_list.length > 0 && jfb_list[jfb_list.length - 1].id
            this.noMoreJfb = this._numberList(jfb_list) < LIMIT
          })
      },

      onTabClick (index) {
        this.activeIndex = index
      },

      infiniteKabi (done) {
        if (this.noMoreKabi) {
          done()
          return
        }

        this._withdrawApplyList({type: 1, offset: this.kabiOffset, limit: LIMIT}).then((payload) => {
          setTimeout(() => {
            done()
          }, 100)
        })
      },

      infiniteJfb (done) {
        if (this.noMoreJfb) {
          done()
          return
        }
        this._withdrawApplyList({type: 2, offset: this.jfbOffset, limit: LIMIT}).then((payload) => {
          setTimeout(() => {
            done()
          }, 100)
        })
      },

      _withdrawApplyList (params) {
        return this.withdrawApplyList(params)
          .then(res => res.data && res.data.payload)
          .then(payload => {
            if (params.type === 1) {
              this.kabiList = this.kabiList.concat(payload)
              this.kabiOffset = payload.length > 0 && payload[payload.length - 1].id
              this.noMoreKabi = this._numberList(payload) < LIMIT
            } else {
              this.jfbList = this.jfbList.concat(payload)
              this.jfbOffset = payload.length > 0 && payload[payload.length - 1].id
              this.noMoreJfb = this._numberList(payload) < LIMIT
            }
          })
      },

      _numberList (list) {
        return list.length
      },

      back () {
        $router.back('/user')
      }
    }
  }
</script>



