<template>
  <div>
    <div v-if="list.length == 0" class="img-wrap">
      <img class="no-data-img" src="//assets.qkcdn.com/images/cdf0ba05cfda6278dfe4e059d5256bed.png">
    </div>
    <scroller ref="withdrawApplyScroller" class="scroller" v-if="list.length > 0" :onInfinite="infinite" noDataText="">
      <div v-for="item in list" class="clearfix withdraw-items">
        <div style="display: none;" class="f-l l-wrap">
          <img class="type-img" src="//qianka.b0.upaiyun.com/images/54ce3a54388b0453828b9d550b0e4908.png">
        </div>
        <div class="f-l m-wrap">
          <div class="desc"><span>{{item.object_type | objectTypeFilter}}</span><span v-text="item.price" class="c0"></span>{{item.object_type | unitFilter}}</div>
          <div class="date" v-text="item.create_time"></div>
        </div>
        <div @click="popupWithdrawFailBox(item)" :class="{'c0 btn': item.process_status == '审核失败', c49: item.process_status == '已打款', c42: item.process_status == '审核中'}" class="f-r r-wrap" v-text="item.process_status"></div>
      </div>
      <div style="height: 70px;"></div>
    </scroller>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .img-wrap {
    width: 360px;
    height: 277px;
    position: absolute;
    margin-top: 30px;
    left: 50%;
    margin-left: -180px;

    .no-data-img {
      width: 100%;
      height: auto;
    }
  }

  .scroller {
    margin-top: 136px;

    .f-l {
      float: left;
    }

    .f-r {
      float: right;
    }

    .clearfix::before,
    .clearfix::after {
      display: table;
      content: '';
    }

    .clearfix::after {
      clear: both;
    }

    .ceb {
      color: #ef502b;
    }

    .withdraw-items {
      padding: 0 20px;
      margin-bottom: 40px;

      .l-wrap .type-img {
        width: 32px;
        height: 34px;
        margin-top: 4px;
      }

      .m-wrap {
        text-align: left;

        .desc {
          font-size: 14px;
          color: #484746;
          margin-bottom: 5px;
        }

        .date {
          @include font-light();

          font-size: 12px;
          color: #B5B5B5;
          letter-spacing: 0;
          line-height: 17px;
        }
      }

      .r-wrap {
        font-size: 13px;
        color: #EF4F2A;
        letter-spacing: 0;
        line-height: 42px;

        @include font-semibold()
      }

      .c0 {
        color: #ef502b;
      }

      .c0.btn {
        height: 30px;
        border-radius: 2px;
        text-decoration: underline;
        line-height: 30px;
        margin-top: 6px;
        font-size: 12px;
      }

      .c42 {
        color: #4A90E2;
      }

      .c49 {
        color: #3A3A3A;
      }
    }
  }
</style>
<script>
  import {popup_withdraw_fail} from '../popup'

  export default {
    props: {
      infinite: Function,
      list: Array
    },

    filters: {
      objectTypeFilter (val) {
        switch (val) {
          case 2001:
            return '手机充值'
          case 2002:
            return '支付宝提现'
          case 2003:
            return '微信提现'
          case 2004:
            return '夺宝币充值'
          default:
            return '集分宝兑换'
        }
      },

      unitFilter (val) {
        if (val) {
          return '元'
        } else {
          return '个'
        }
      }
    },

    methods: {
      popupWithdrawFailBox (item) {
        if (item.process_status === '审核失败') {
          let popup = $popup.fromTemplate(popup_withdraw_fail.template(item.error_string), popup_withdraw_fail.options)
          popup.show()
        }
      }
    }
  }
</script>



